body {
    background-color: #F5EDDC;
    font-family: "Didot", serif;
    display: flex;
    flex-direction: column;
    align-items: center;
    position: relative;
}
  
.header-container {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin: 10px;
}
  
h1 {
    font-size: 40px;
    font-weight: bold;
    margin-top: 20px;
    margin-bottom: 20px;
}
  
h2 {
    font-size: 30px;
    margin: 5px;
    text-align: center;
}
  
.data-dropdown {
    display: flex;
    flex-direction: row;
    padding: 20px;
    align-items: center;
}
  
.dropdown-container {
    padding: 15px;
}
  
.dropdown-menu {
    font-family: "Didot", serif;
    font-size: 20px;
}
  
.content-container {
    display: flex;
    flex-direction: row-reverse;
}
  
.graph-container {
    margin: 10px;
    padding: 10px;
    border-style: solid;
    border-color: black;
    border-width: 1px;
}
  
.legend {
    font-size: 20px;
    margin: 20px;
}
  
.footer {
    margin: 20px;
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    align-items: center;
}
  
.data-link {
    font-size: 20px;
    margin: 20px;
}
  
.social-links {
    font-size: 30px;
    margin: 20px;
}
  
a:link, a:visited {
    background-color: #A2B5BB;
    color: white;
    padding: 14px 25px;
    text-align: center;
    text-decoration: none;
    display: inline-block;
}
  
a:hover, a:active {
    background-color: #EB1D36;
    box-shadow: 0 0 10px rgba(150, 150, 150, .8);
}

.tooltip {
    font-size: 20px;
    font-weight: bold;
    position: absolute;
    width: auto;
    height: auto;
    padding: 10px;
    background-color: white;
    border: solid;
    border-width: 3px;
    -webkit-border-radius: 10px;
    -moz-border-radius: 10px;
    border-radius: 10px;
    -webkit-box-shadow: 0 0 10px rgba(150, 150, 150, .8);
    -moz-box-shadow: 0 0 10px rgba(150, 150, 150, .8);
    box-shadow: 0 0 10px rgba(150, 150, 150, .8);
}